.fade-in-linear-enter-active,
.fade-in-linear-leave-active {
  transition: opacity 200ms linear; }

.fade-in-linear-enter,
.fade-in-linear-leave,
.fade-in-linear-leave-active {
  opacity: 0; }

/** tu-fade-in-linear */
.tu-fade-in-easeout-enter-active,
.tu-fade-in-easeout-leave-active {
  transition: opacity 0.3s ease-out; }

.tu-fade-in-easeout-enter,
.tu-fade-in-easeout-leave,
.tu-fade-in-easeout-leave-active {
  opacity: 0; }

/** tu-fade-in */
.tu-fade-in-enter-active,
.tu-fade-in-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-fade-in-enter,
.tu-fade-in-leave-active {
  opacity: 0; }

/** tu-popper-in-center */
.tu-popper-in-center-enter-active,
.tu-popper-in-center-leave-active {
  transition: all 0.2s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-popper-in-center-enter,
.tu-popper-in-center-leave-active {
  scale: 0;
  transform: scaleX(0); }

/** tu-zoom-in-center */
.tu-zoom-in-center-enter-active,
.tu-zoom-in-center-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-zoom-in-center-enter,
.tu-zoom-in-center-leave-active {
  opacity: 0;
  transform: scaleX(0); }

/** tu-zoom-in-top */
.tu-zoom-in-top-enter-active,
.tu-zoom-in-top-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center top; }

.tu-zoom-in-top-enter,
.tu-zoom-in-top-leave-active {
  opacity: 0;
  transform: scaleY(0); }

.tu-zoom-in-bottom-enter-active,
.tu-zoom-in-bottom-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center bottom; }

.tu-zoom-in-bottom-enter,
.tu-zoom-in-bottom-leave-active {
  opacity: 0;
  transform: scaleY(0); }

/** tu-zoom-in-left */
.tu-zoom-in-left-enter-active,
.tu-zoom-in-left-leave-active {
  opacity: 1;
  transform: scale(1, 1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: top left; }

.tu-zoom-in-left-enter,
.tu-zoom-in-left-leave-active {
  opacity: 0;
  transform: scale(0.45, 0.45); }

.collapse-transition {
  transition: 0.3s height ease-in-out, 0.3s padding-top ease-in-out, 0.3s padding-bottom ease-in-out; }

.horizontal-collapse-transition {
  transition: 0.3s width ease-in-out, 0.3s padding-left ease-in-out, 0.3s padding-right ease-in-out; }

.tu-list-enter-active,
.tu-list-leave-active {
  transition: all 0.48s; }

.tu-list-enter,
.tu-list-leave-active {
  opacity: 0;
  transform: translateX(-40px); }

.tu-cascader-menu-enter-active,
.tu-cascader-menu-leave-active {
  transition: all 0.3s; }

.tu-cascader-menu-enter,
.tu-cascader-menu-leave-active {
  opacity: 0;
  margin-right: -180px; }

.tu-opacity-transition {
  transition: opacity 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

html,
body {
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: #e4ebf5; }

body {
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  color: #8394be;
  -webkit-font-smoothing: antialiased; }

/* BEM [block]__[element]--[modifier]
 -------------------------- */
.tu-carousel {
  position: relative; }
  .tu-carousel--horizontal {
    overflow-x: hidden; }
  .tu-carousel--vertical {
    overflow-y: hidden; }
  .tu-carousel__container {
    position: relative;
    height: 300px; }
  .tu-carousel__arrow {
    border: none;
    outline: none;
    padding: 0;
    margin: 0;
    height: 36px;
    width: 36px;
    cursor: pointer;
    transition: 0.3s;
    border-radius: 50%;
    background-color: rgba(31, 45, 61, 0.11);
    color: #ffffff;
    position: absolute;
    top: 50%;
    z-index: 10;
    transform: translateY(-50%);
    text-align: center;
    font-size: 12px; }
    .tu-carousel__arrow--left {
      left: 16px; }
    .tu-carousel__arrow--right {
      right: 16px; }
    .tu-carousel__arrow:hover {
      background-color: rgba(31, 45, 61, 0.23); }
    .tu-carousel__arrow i {
      cursor: pointer; }
  .tu-carousel__indicators {
    position: absolute;
    list-style: none;
    margin: 0;
    padding: 0;
    z-index: 2; }
    .tu-carousel__indicators--horizontal {
      bottom: 0;
      left: 50%;
      transform: translateX(-50%); }
    .tu-carousel__indicators--vertical {
      right: 0;
      top: 50%;
      transform: translateY(-50%); }
    .tu-carousel__indicators--outside {
      bottom: 26px;
      text-align: center;
      position: static;
      transform: none; }
      .tu-carousel__indicators--outside .tu-carousel__indicator:hover button {
        opacity: 0.64; }
      .tu-carousel__indicators--outside button {
        background-color: #8394be;
        opacity: 0.24; }
    .tu-carousel__indicators--labels {
      left: 0;
      right: 0;
      transform: none;
      text-align: center; }
      .tu-carousel__indicators--labels .tu-carousel__button {
        height: auto;
        width: auto;
        padding: 2px 18px;
        font-size: 12px; }
      .tu-carousel__indicators--labels .tu-carousel__indicator {
        padding: 6px 4px; }
  .tu-carousel__indicator {
    background-color: transparent;
    cursor: pointer; }
    .tu-carousel__indicator:hover button {
      opacity: 0.72; }
    .tu-carousel__indicator--horizontal {
      display: inline-block;
      padding: 12px 4px; }
    .tu-carousel__indicator--vertical {
      padding: 4px 12px; }
      .tu-carousel__indicator--vertical .tu-carousel__button {
        width: 2px;
        height: 15px; }
    .tu-carousel__indicator.is-active button {
      opacity: 1; }
  .tu-carousel__button {
    display: block;
    opacity: 0.48;
    width: 30px;
    height: 2px;
    background-color: #ffffff;
    border: none;
    outline: none;
    padding: 0;
    margin: 0;
    cursor: pointer;
    transition: 0.3s; }

.carousel-arrow-left-enter,
.carousel-arrow-left-leave-active {
  transform: translateY(-50%) translateX(-10px);
  opacity: 0; }

.carousel-arrow-right-enter,
.carousel-arrow-right-leave-active {
  transform: translateY(-50%) translateX(10px);
  opacity: 0; }
